/* */
body {
	font-family: "Noto Sans JP", "sans-serif";
	margin: 0;
	min-width: 1076px;
}


.color-green {
	color: #00abae;
}

.sub-title {
	font-size: 32px;
}

.block-header {
	margin: 10px 0;
	padding: 0 20px 10px;
	box-shadow: 0 10px 10px -5px rgba(204, 204, 204, 0.3);
}

.block-header a {
	font-size: 40px;
	color: #000;
	text-decoration: none;
	letter-spacing: 1px;
}

.block-main {
	text-align: center;
	margin: 100px auto;
}

.item-list {
	width: 100%;
	max-width:384px;
	margin: 0 auto;
}

.item-list img {
	border-radius: 50%;
}
.item-list img.youtube {
	border-radius: 0%;
}


.item-list b {
	display: block;
	font-size:24px;
	font-weight: normal;
	margin: 20px 0 30px;
}

.item-list p {
	font-size: 15px;
	line-height: normal;
	text-align: left;
}

.icon-list {
	list-style: none;
	width: 100%;
	max-width: 247px;
	margin: 30px auto;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.icon-list li {
	margin-right: 22px;
}

.icon-list li:last-child {
	margin-right: 0;
}

.icon-list li img {
	width: 54px;
	height: 52px;
}

.icon-list li img.youtube {
	width: 54px;
	height: 52px;
}



.box-works {
	margin: 80px 0 0;
}

.works-list {
	list-style: none;
	width: 100%;
	max-width: 1076px;
	margin: 30px auto;
	padding: 0;
	text-align: left;
	overflow: hidden;
}

.works-list li {
	display: block;
	width: 100%;
	max-width: 348px;
	float: left;
	margin-right: 10px;
}

.works-list li:lastchild {
	margin-right: 0;
}

.works-list li img {
	width: 100%;
	border: 1px solid #ccc;	
}

.works-list li h3 {
	font-size: 24px;
	margin: 10px 0;
}

.works-list li span {
	font-size: 18px;
}

.works-list li .link {
	display: flex;
	justify-content: left;
	align-items: left;
	margin-top: 15px;
}

.works-list li .link a {
	background-color: rgb(226, 226, 226);
	padding: 5px 20px;
	margin-right: 10px;
	font-size: 15px;
	text-decoration: none;
	color: #000;
}

.box-query {
	margin: 80px 0 0;
}

.block-footer p {
	clear: both;
	text-align: center;
	font-size: 15px;
	margin: 50px 0 20px;
}



