/*
 * スマホ向けCSS
 */
@media screen and (max-width: 767px) {
	body {
		min-width: 100%;
	}
	
	.block-header {
		margin: 10px 0;
		padding: 0 10px 10px;
		box-shadow: 0 8px 8px 5px rgba(204, 204, 204, 0.3);
	}
	
	.block-header a {
		font-size: 31px;
	}
	
	.block-main {
		margin: 50px 0;
	}

	.box-profile {
		padding: 0 10px;
	}
	
	.item-list b {
		font-size: 18px;
	}

	.item-list {
		max-width: 100%;
	}
	
	.icon-list {
		max-width: 100%;
	}
	
	.icon-list {
		margin-right: 25px;
	}
	
	.box-works {
		margin: 60px 0 0;
		padding: 0 10px;
	}
	
	.works-list {
		max-width: 100%;
	}
	
	.works-list li {
		max-width: 100%;
		float: none;
		margin-right: 0px;
		margin-bottom: 30px;
	}
	
	.works-list h3 {
		font-size: 20px;
		margin: 5px 0;
	}
	
	.works-list span {
		font-size: 14px;
	}
	
	
}

